#projects {
  margin: 3% 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#buttons {
  width:50%;
  margin:auto;
  margin-bottom: 2%;
  display:inline;
}

.nav-section {
  margin: 3% 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.nav-section > .project-item {
  display: inline-block;
  pointer-events: auto;
  cursor: pointer;
}


.nav-section > .project-item:hover {
  text-decoration: underline;
}

.project{
  /* background-color: #F8DFDB; */
  display:flex;
  width:85%;
  margin: auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#btn-group{
  display:flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

#heart {
  position:'absolute';
  margin:auto
}

.separator {
  display: flex;
  align-items: center;
  text-align: center;
  font-size: 1.5em;
}

.separator::before,
.separator::after {
  content: '';
  flex: 1;
  border-bottom: 1px solid #000;
}

.separator:not(:empty)::before {
  margin-right: .25em;
}

.separator:not(:empty)::after {
  margin-left: .25em;
}

/* ---------- footer ------------- */

#footer {
  position:inherit;
  text-align:center;
  font-family: Lato;
  font-weight: 300;
  font-size: smaller;
  bottom: 0;
  width: 100%;
  margin-top: 5%
}

p > div > a:hover{
  animation-name: buttonhover;
  animation-duration:4s;
}

@keyframes buttonhover {
  0% {width: 40px; height:40px}
  25% {width:30px; height:30px}
  50% {width: 40px; height:40px}
}